// src/faq.scss
body {
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
    margin: 0;
    padding: 0;
}

.faq {
    max-width: 600px;
    margin: 20px auto;
    background: #fff;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.faq-item {
    border-bottom: 1px solid #ddd;
}

.faq-item:last-child {
    border-bottom: none;
}

.faq-question {
    width: 100%;
    background: #007bff;
    color: #fff;
    padding: 15px;
    border: none;
    text-align: left;
    font-size: 16px;
    cursor: pointer;
    outline: none;
}

.faq-answer {
    display: none;
    padding: 15px;
    background: #f9f9f9;
}

.faq-item.active .faq-answer {
    display: block;
}
